@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --ff-kr: 'Noto Sans KR', sans-serif;
  --ff-kr: 'IBM Plex Mono', monospace;
  color-scheme: dark;
}

.container {
  margin: auto;
  padding: 1rem;
  width: 100%;
}

@media (max-width: 1024px) {
  .container {
    max-width: 100%;
  }
}

